<template>
  <div class="stepArea container">
    <div class="step-title-container">
      <span class="step-title"
        >竞拍流程</span
      >
    </div>
    <div
      class="step-item"
      @mouseenter="mouseEnter(index)"
      @mouseleave="mouseLeave"
      :class="{
        active:
          moveActive === index ||
          active === index,
      }"
      v-for="(item, index) in stepList"
      :key="item.label + index"
    >
      <span class="step-num">{{
        index + 1
      }}</span>
      <div class="step-txt">
        <span class="step-label">{{
          item.label
        }}</span>
        <br />
        <span class="step-name">{{
          item.name
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      moveActive: null,
      stepList: [
        {
          label: "第一步",
          name: "阅读公告",
        },
        {
          label: "第二步",
          name: "标的详情",
        },
        {
          label: "第三步",
          name: "交保证金",
        },
        {
          label: "第四步",
          name: "开始竞价",
        },
        {
          label: "第五步",
          name: "竞价成功",
        },
        {
          label: "第六步",
          name: "办理交割",
        },
      ],
    };
  },
  mounted() {
    this.intervalActive()
  },
  destroyed() {
    if (this.timeout) {
      clearTimeout(this.timeout);
      this.timeout = null
    }
  },
  methods: {
    intervalActive() {
      this.timeout = setTimeout(() => {
        clearTimeout(this.timeout)
        this.timeout = null
        if (this.active === 5) {
          this.active = 0;
        } else {
          this.active += 1;
        }
        this.intervalActive()
      }, 1000);
    },
    mouseEnter(index) {
      this.moveActive = index;
    },
    mouseLeave() {
      this.moveActive = null;
    },
  },
};
</script>

